import { fabric } from 'fabric';
import { CanvasAnnotator } from '../CanvasAnnotator';


export class AddImageControl {
    private canvasAnnotator: CanvasAnnotator;

    constructor(annotator: CanvasAnnotator) {
        this.canvasAnnotator = annotator;
    }

    public async pickImageAndAddToCanvas(): Promise<void> {
        const image = await this.selectImageFile();
        if (!image) return;

        const { width, height, scale } = this.calculateScaledDimensions(image);

        const fabricImage = new fabric.Image(image, {
            left: Math.floor((this.canvasAnnotator.fcanvas.width! / 2) - (width / 2)),
            top: Math.floor((this.canvasAnnotator.fcanvas.height! / 2) - (height / 2)),
            scaleX: scale,
            scaleY: scale,
            lockUniScaling: true
        });

        this.canvasAnnotator.configureObject(fabricImage);
        this.canvasAnnotator.fcanvas.add(fabricImage);
        this.canvasAnnotator.fcanvas.setActiveObject(fabricImage);
        this.canvasAnnotator.getUndoRedoManager().addObject([fabricImage]);
    }

    private selectImageFile(): Promise<HTMLImageElement | null> {
        return new Promise((resolve) => {
            const input = document.createElement("input");
            input.type = "file";
            input.accept = "image/*";
            input.onchange = () => {
                const file = input.files?.[0];
                if (!file) return resolve(null);
                const reader = new FileReader();
                reader.onload = () => {
                    const img = new Image();
                    img.onload = () => resolve(img);
                    img.onerror = () => resolve(null);
                    img.src = reader.result as string;
                };
                reader.readAsDataURL(file);
            };
            input.click();
        });
    }

    private calculateScaledDimensions(image: HTMLImageElement) {
        const canvas = this.canvasAnnotator.fcanvas;
        const scaleWidth = (canvas.width! - 40) / image.width;
        const scaleHeight = (canvas.height! - 40) / image.height;
        const scale = Math.min(scaleWidth, scaleHeight, 1);
        return {
            width: scale * image.width,
            height: scale * image.height,
            scale
        };
    }
}
