import { CanvasAnnotator } from '../CanvasAnnotator';

const colorList = [
    "#ff260b", "#ff920b", "#fff20b", "#0bffa5", "#0bb2ff",
    "#0b47ff", "#750bff", "#ff0bd2", "#ffffff", "#000000"
];

export class ColorPicker {
    private canvasAnnotator: CanvasAnnotator;
    private isOpenState: boolean = false;
    private colorPickerElement: HTMLElement | null = null;

    constructor(annotator: CanvasAnnotator) {
        this.canvasAnnotator = annotator;
    }
    
    public isOpen = () => this.isOpenState;

    public toggle(): void {
        this.isOpenState ? this.close() : this.open();
    }

    public open(): void {
        if (this.isOpenState) return;
        this.isOpenState = true;

        const pickerButton = document.getElementById("canvas-colorpicker")!;
        // const buttonRect = pickerButton.getBoundingClientRect();

        this.colorPickerElement = document.createElement("div");
        this.colorPickerElement.className = "canvas--colorpicker";
        
        colorList.forEach(color => {
            const colorButton = document.createElement("div");
            colorButton.className = "color-button";
            colorButton.style.backgroundColor = color;
            colorButton.addEventListener("click", () => {
                this.canvasAnnotator.setCurrentColor(color);
                this.close();
            });
            this.colorPickerElement!.appendChild(colorButton);
        });

        // document.body.appendChild(this.colorPickerElement);
        // .canvas-colorpicker
        document.querySelector("#canvas-colorpicker-wrapper")!.append(this.colorPickerElement)

        // const pickerRect = this.colorPickerElement.getBoundingClientRect();
        // this.colorPickerElement.style.left = `${buttonRect.left + window.scrollX - pickerRect.width - 10}px`;
        // this.colorPickerElement.style.top = `${buttonRect.top + window.scrollY + (buttonRect.height / 2) - (pickerRect.height / 2)}px`;
    }

    public close(): void {
        if (!this.isOpenState || !this.colorPickerElement) return;
        this.isOpenState = false;
        this.colorPickerElement.remove();
        this.colorPickerElement = null;
    }
}
