import { fabric } from 'fabric';
import { CanvasAnnotator } from '../CanvasAnnotator';


export class CircleControl {
    private canvasAnnotator: CanvasAnnotator;
    private activeObject: fabric.Ellipse | null = null;
    private startPosition: { x: number, y: number };


    constructor(annotator: CanvasAnnotator) {
        this.canvasAnnotator = annotator;
        this.startPosition = {x:0 , y:0}
    }
    
    public onMouseDown(position: { x: number, y: number }): void {
        this.startPosition = position;
        this.activeObject = new fabric.Ellipse({
            left: position.x,
            top: position.y,
            rx: 0,
            ry: 0,
            stroke: this.canvasAnnotator.currentColor,
            strokeWidth: 5,
            fill: "transparent",
        });
        this.canvasAnnotator.fcanvas.add(this.activeObject);
    }
    
    public onMouseMove(rect: { x1: number, y1: number, x2: number, y2: number }): void {
       if (!this.activeObject) return;
       const width = rect.x2 - rect.x1;
       const height = rect.y2 - rect.y1;
       this.activeObject.set({
           left: rect.x1,
           top: rect.y1,
           rx: width / 2,
           ry: height / 2,
           width: width,
           height: height,
       });
    }
    
    public onMouseUp(): fabric.Object | null {
       if (!this.activeObject) return null;
       if (this.activeObject.width! < 5 && this.activeObject.height! < 5) {
            this.canvasAnnotator.fcanvas.remove(this.activeObject);
            this.activeObject = null;
            return null;
        }
        const finalObject = this.activeObject;
        this.activeObject = null;
        return finalObject;
    }
}
