import { fabric } from 'fabric';
import { CanvasAnnotator } from '../CanvasAnnotator';

export class ArrowControl {
    private canvasAnnotator: CanvasAnnotator;
    private line: fabric.Line | null = null;
    private head: fabric.Polygon | null = null;
    private startPosition: { x: number, y: number };

    constructor(annotator: CanvasAnnotator) {
        this.canvasAnnotator = annotator;
        this.startPosition = {x:0 , y:0}
    }

    public onMouseDown(position: { x: number, y: number }): void {
        this.startPosition = position;
        const coords = [position.x, position.y, position.x, position.y];
        this.line = new fabric.Line(coords, {
            stroke: this.canvasAnnotator.currentColor,
            strokeWidth: 5,
            originX: "center",
            originY: "center",
        });
        this.line.shadow = new fabric.Shadow({
            offsetX: 0,
            offsetY: 1,
            blur: 2,
            color: "rgba(0, 0, 0, .2)",
            affectStroke: true
        });
        this.head = new fabric.Polygon([
            { x: 0, y: 0 },
            { x: 7, y: -10 },
            { x: 14, y: 0 },
            { x: 7, y: -3 }
        ], {
            fill: this.canvasAnnotator.currentColor,
            stroke: this.canvasAnnotator.currentColor,
            left: position.x,
            top: position.y,
            strokeLineJoin: "round",
            originX: "center",
            originY: "center",
            paintFirst: "stroke",
        });
        this.head.shadow = new fabric.Shadow({
            offsetX: 0,
            offsetY: 1,
            blur: 2,
            color: "rgba(0, 0, 0, .2)",
            affectStroke: true
        });

        this.canvasAnnotator.fcanvas.add(this.line, this.head);
    }
    
    
    
    public onMouseMove(position: { x: number, y: number }): void {
        if (!this.line || !this.head) return;
        this.line.set({ x2: position.x, y2: position.y });
        
        const angle = Math.atan2(position.y - this.startPosition.y, position.x - this.startPosition.x) * 180 / Math.PI + 90;
        this.head.set({ left: position.x, top: position.y, angle: angle });
    }
    
    public onMouseUp(position: { x: number, y: number }): fabric.Object | null {
        if (!this.line || !this.head) return null;
        const dx = position.x - this.startPosition.x;
        const dy = position.y - this.startPosition.y;
        if (Math.sqrt(dx * dx + dy * dy) < 10) {
            this.canvasAnnotator.fcanvas.remove(this.line, this.head);
            this.line = null;
            this.head = null;
            return null;
        }
        
        const group = new fabric.Group([this.line, this.head], { lockScalingFlip: true });
        this.canvasAnnotator.fcanvas.remove(this.line, this.head);
        this.canvasAnnotator.fcanvas.add(group);
        this.line = null;
        this.head = null;
        return group;
    }
}
