import { fabric } from 'fabric';
import { CanvasAnnotator } from '../CanvasAnnotator';

export class TextControl {
    private canvasAnnotator: CanvasAnnotator;
    private selectionRect: fabric.Rect | null = null;
    private startPosition: { x: number, y: number };


    constructor(annotator: CanvasAnnotator) {
        this.canvasAnnotator = annotator;
        this.startPosition = {x:0 , y:0}
    }

    public onMouseDown(position: { x: number, y: number }): void {
        this.startPosition = position;
        this.selectionRect = new fabric.Rect({
            left: position.x,
            top: position.y,
            width: 0,
            height: 0,
            stroke: "rgb(0, 0, 0)",
            fill: "rgba(0, 0, 0, 0.2)",
            strokeWidth: 1,
            strokeDashArray: [3, 3],
        });
        this.canvasAnnotator.fcanvas.add(this.selectionRect);
    }
    
    public onMouseMove(rect: { x1: number, y1: number, x2: number, y2: number }): void {
        if (!this.selectionRect) return;
        this.selectionRect.set({
            left: rect.x1,
            top: rect.y1,
            width: rect.x2 - rect.x1,
            height: rect.y2 - rect.y1,
        });
    }
    
    public async onMouseUp(): Promise<fabric.Object | null> {
        if (!this.selectionRect) return null;

        const rectWidth = this.selectionRect.width!;
        const rectHeight = this.selectionRect.height!;
        this.canvasAnnotator.fcanvas.remove(this.selectionRect);
        
        // Don't create textbox if selection area is too small
        if (rectWidth < 10 || rectHeight < 10) {
            this.selectionRect = null;
            return null;
        }

        const textbox = new fabric.Textbox("Type something", {
            left: this.selectionRect.left,
            top: this.selectionRect.top,
            width: rectWidth,
            fontSize: Math.max(12, Math.min(rectHeight * 0.8, 48)),
            fill: this.canvasAnnotator.currentColor,
            fontFamily: "Helvetica, sans-serif",
            lockUniScaling: false,
            stroke: "white",
            strokeWidth: 4,
            paintFirst: "stroke",
        });

        this.canvasAnnotator.fcanvas.add(textbox);
        this.canvasAnnotator.fcanvas.setActiveObject(textbox);
        textbox.enterEditing();
        textbox.selectAll();
        
        this.selectionRect = null;
        return textbox;
    }
}
