import { CanvasAnnotator } from './CanvasAnnotator';

// Configuration for the toolbar controls
const controlsConfig = [
    { id: 'canvas-arrow', control: 'arrow', key: 'A' },
    { id: 'canvas-rectangle', control: 'rectangle', key: 'R' },
    { id: 'canvas-circle', control: 'circle', key: 'O' },
    { id: 'canvas-uploadimage', control: 'image', key: 'I' },
    { id: 'canvas-pen', control: 'pen', key: 'P' },
    { id: 'canvas-blur', control: 'blur', key: 'B' },
    { id: 'canvas-emojii', control: 'emojii', key: 'E' },
    { id: 'canvas-text', control: 'text', key: 'T' },
];

// Configuration for undo, redo, and color picker buttons
const undoRedoButtonsConfig = [
    { id: 'canvas-colorpicker', control: 'color', key: 'C' },
    { id: 'undoButton', control: 'undo', key: 'Z' },
    { id: 'redoButton', control: 'redo', key: 'Y' }
];

// Configuration for utility buttons like download and copy
const utilityConfig = [
    { id: 'canvas-download', key: 'S', control: 'download' },
    { id: 'canvas-copy', key: 'C', control: 'copy' },
];

export class CanvasAnnotating {
    private container!: HTMLElement;
    private toolbar!: HTMLElement;
    private canvas!: HTMLCanvasElement;
    private canvasAnnotator!: CanvasAnnotator;

    constructor() {
        this.createDOM();
    }

    /**
     * Creates the basic DOM structure for the canvas editor.
     */
    private createDOM(): void {
        const html = `
            <div id="canvas-editor-container">
                <div class="canvas-canvas-container">
                    <div class="canvas--toolbar" style="display: none;"></div>
                    <canvas id="canvas--canvas"></canvas>
                </div>
                <div class="canvas-form-container"></div>
            </div>`;

        // document.body.innerHTML = html;
        document.body.insertAdjacentHTML('beforeend', html);
        this.container = document.getElementById("canvas-editor-container")!;
        this.toolbar = this.container.querySelector(".canvas--toolbar")!;
        this.canvas = this.container.querySelector("#canvas--canvas") as HTMLCanvasElement;
    }

    /**
     * Initializes the canvas annotator and sets up the toolbar.
     * @param base64Image The base64 encoded image to load into the canvas.
     * @param isOverlay A flag to determine if the canvas should act as an overlay.
     */
    public init(base64Image: string, isOverlay: boolean): void {
        this.canvasAnnotator = new CanvasAnnotator(this.canvas, isOverlay);
        this.canvasAnnotator.setScreenshot({
            base64: base64Image,
            width: 0, // Will be determined from the image
            height: 0, // Will be determined from the image
            pixelRatio: window.devicePixelRatio,
        });

        

        if (isOverlay) {
            this.setupOverlay();
        } else {
            this.toolbar.style.display = "flex";
            this.initializeControls();
            this.initializeUndoRedoButtons();
            this.initializeUtilityButtons();
        }
    }

    /**
     * Sets up an overlay for capturing mouse events before activating the editor.
     */
    private setupOverlay(): void {
        const overlay = this.canvasAnnotator.initOverlay();
        if (overlay) {
            this.canvas.parentNode?.insertBefore(overlay, this.canvas.nextSibling);
            overlay.addEventListener('click', (e: MouseEvent) => {
                const elementRect = (e.target as HTMLElement).getBoundingClientRect();
                const scrollX = document.body.scrollLeft;
                const scrollY = document.body.scrollTop;
                const calculatedMousePos = {
                    mouseX: e.clientX - elementRect.left,
                    mouseY: e.clientY - elementRect.top,
                    scrollX: scrollX,
                    scrollY: scrollY
                };

                (window as any)._mousePos = calculatedMousePos;
                overlay.remove();
                this.toolbar.style.display = "flex";
                this.initializeControls();
                this.initializeUndoRedoButtons();
                this.initializeUtilityButtons();
            });
        }
    }

    /**
     * Initializes the main drawing and editing controls.
     */
    private initializeControls(): void {
        const controlContainer = document.createElement("div");
        controlContainer.className = "canvas--controls";
        for (const control of controlsConfig) {
            const button = this.createButton(control.id, control.control, control.key);
            button.addEventListener("click", () => this.canvasAnnotator.toggleTool(control.control));

            if(control.control === "emojii"){
                const buttonWrapper = document.createElement("div");
                buttonWrapper.className = "canvas--button-wrapper";
                buttonWrapper.id = `${control.id}-wrapper`;
                buttonWrapper.appendChild(button);
                controlContainer.appendChild(buttonWrapper);
            }else{
                controlContainer.appendChild(button);
            }

        }
        this.toolbar.appendChild(controlContainer);
    }

    /**
     * Initializes the undo, redo, and color picker buttons.
     */
    private initializeUndoRedoButtons(): void {
        const undoRedoContainer = document.createElement("div");
        undoRedoContainer.className = "canvas--undo-redo";
        for (const config of undoRedoButtonsConfig) {
            const button = this.createButton(config.id, config.control, config.key);
            if (config.key === "Z" || config.key === "Y") {
                button.classList.add("disabled");
            }
            if (config.key === "C") {
                button.innerHTML = '<div class="dots" style="background-color: red;"></div>';
                button.addEventListener("click", () => this.canvasAnnotator.getColorPicker().toggle());
            } else if (config.key === "Z") {
                button.addEventListener("click", () => this.canvasAnnotator.getUndoRedoManager().undo());
            } else if (config.key === "Y") {
                button.addEventListener("click", () => this.canvasAnnotator.getUndoRedoManager().redo());
            }

            
            
            if(config.control === "color"){
                const buttonWrapper = document.createElement("div");
                buttonWrapper.className = "canvas--button-wrapper";
                buttonWrapper.id = `${config.id}-wrapper`;
                buttonWrapper.appendChild(button);
                undoRedoContainer.appendChild(buttonWrapper);
            }else{
                undoRedoContainer.appendChild(button);
            }

        }
        this.toolbar.appendChild(undoRedoContainer);
    }

    /**
     * Initializes utility buttons like download and copy.
     */
    private initializeUtilityButtons(): void {
        const utilityContainer = document.createElement("div");
        utilityContainer.className = "canvas--utility";
        for (const config of utilityConfig) {
            const button = this.createButton(config.id, config.control, config.key);
            button.addEventListener("click", () => this.canvasAnnotator.toggleToolAction(config.control));
            utilityContainer.appendChild(button);
        }
        this.toolbar.appendChild(utilityContainer);
    }
    
    /**
     * Helper function to create a button element.
     * @param id The ID for the button.
     * @param tool The tool associated with the button.
     * @param text The text or key to display on the button.
     * @returns The created button element.
     */
    private createButton(id: string, tool: string, text: string): HTMLDivElement {
        const button = document.createElement("div");
        button.id = id;
        button.dataset.tool = tool;
        button.innerHTML = text;
        return button;

        // // ButtonWrapper
        // const buttonWrapper = document.createElement("div");
        // buttonWrapper.className = "canvas--button-wrapper";
        // buttonWrapper.id = `${id}-wrapper`;
        // buttonWrapper.appendChild(button);
        // return buttonWrapper;
    }

    distroy() {
        this.container.remove();
    }
}

