import { fabric } from 'fabric';
import { ColorPicker } from './controls/ColorPicker';
import { EmojiControl } from './controls/EmojiControl';
import { UndoRedoManager } from './UndoRedoManager';
import { AddImageControl } from './controls/AddImageControl';
import { TextControl } from './controls/TextControl';
import { RectangleControl } from './controls/RectangleControl';
import { CircleControl } from './controls/CircleControl';
import { ArrowControl } from './controls/ArrowControl';
import { PencilControl } from './controls/PencilControl';
import { BlurControl } from './controls/BlurControl';


fabric.Object.prototype.set({
    borderColor: 'white',
    cornerColor: '#000',
    cornerStrokeColor: 'white',
    cornerStyle: 'circle', // default is 'rect'
    cornerSize: 7,
    transparentCorners: false,
});


// K-Event Dispatcher for simple event handling
const KEventDispatcher = {
    dispatch: (eventName: string, payload: any) => {
        console.log(`Dispatching event: ${eventName}`, payload);
    }
};

export class CanvasAnnotator {
    public fcanvas: fabric.Canvas;
    public currentColor: string = "#ff260b";
    
    private isOverlay: boolean;
    private screenshot: any = null;
    private currentTool: string | null = null;
    private previousTool: string | null = null;
    private startPosition: { x: number, y: number } | null = null;
    private endPosition: { x: number, y: number } | null = null;
    private isAnnotating: boolean = false;
    private shouldSkipNextAnnotation: boolean = false;
    
    // Controls and Managers
    private colorPicker!: ColorPicker;
    private emojiControl!: EmojiControl;
    private undoRedoManager!: UndoRedoManager;
    private addImageControl!: AddImageControl;
    private textControl!: TextControl;
    private rectangleControl!: RectangleControl;
    private circleControl!: CircleControl;
    private arrowControl!: ArrowControl;
    private pencilControl!: PencilControl;
    private blurControl!: BlurControl;

    constructor(canvasElement: HTMLCanvasElement, isOverlay: boolean) {

        this.isOverlay = isOverlay;
        this.fcanvas = new fabric.Canvas(canvasElement, {
            enableRetinaScaling: true,
            selectionColor: "rgba(0,0,0,0.1)",
            selectionBorderColor: "rgba(0,0,0,0.25)",
            selectionLineWidth: 2,
            renderOnAddRemove: true,
            skipTargetFind: false,
        });

        this.initializeControls();
        this.initializeFabricEvents();
        this.configureKeystrokes();
        this.setCurrentColor(this.currentColor);
    }

    /**
     * Initializes all the control and manager classes.
     */
    private initializeControls(): void {
        this.colorPicker = new ColorPicker(this);
        this.emojiControl = new EmojiControl(this);
        this.undoRedoManager = new UndoRedoManager(this);
        this.addImageControl = new AddImageControl(this);
        this.textControl = new TextControl(this);
        this.rectangleControl = new RectangleControl(this);
        this.circleControl = new CircleControl(this);
        this.arrowControl = new ArrowControl(this);
        this.pencilControl = new PencilControl(this);
        this.blurControl = new BlurControl(this);
    }

    /**
     * Sets up Fabric.js canvas event listeners.
     */
    private initializeFabricEvents(): void {
        this.fcanvas.on("mouse:down", (e) => this.handleMouseDown(e));
        this.fcanvas.on("mouse:move", (e) => this.handleMouseMove(e));
        this.fcanvas.on("mouse:up", () => this.handleMouseUp());
        this.fcanvas.on("path:created", (e) => this.pencilControl.onPathCreated(e as any));
        this.fcanvas.on("selection:created", () => this.handleSelectionCreated());
        this.fcanvas.on("selection:cleared", () => this.handleSelectionCleared());
        this.fcanvas.on("before:selection:cleared", (e) => this.handleBeforeSelectionCleared(e));
    }

    // --- Public Getter Methods ---
    public getColorPicker = () => this.colorPicker;
    public getUndoRedoManager = () => this.undoRedoManager;
    public getEmojiControl = () => this.emojiControl;

    // --- Event Handlers ---
    private handleMouseDown(e: fabric.IEvent): void {
        if (e.target) {
            // (e.target as fabric.Object).uniScaleTransform = e.target.type !== "textbox";
            return;
        }
        if (this.shouldSkipNextAnnotation) {
            this.shouldSkipNextAnnotation = false;
            return;
        }
        if (this.currentTool) {
            const pointer = this.fcanvas.getPointer(e.e);
            this.startPosition = { x: Math.round(pointer.x), y: Math.round(pointer.y) };
            this.endPosition = { ...this.startPosition };
            this.beginAnnotating();
        }
    }

    private handleMouseMove(e: fabric.IEvent): void {
        if (!this.isAnnotating) return;
        const pointer = this.fcanvas.getPointer(e.e);
        this.endPosition = { x: Math.round(pointer.x), y: Math.round(pointer.y) };
        this.continueAnnotating();
    }

    private handleMouseUp(): void {
        if (this.isAnnotating) {
            this.endAnnotating();
        }
    }
    
    private handleSelectionCreated(): void {
        const active = this.fcanvas.getActiveObject();
        if (active?.type === "activeSelection") {
            (active as fabric.ActiveSelection).set({
                borderColor: "rgb(0, 0, 0)",
                lockUniScaling: true,
            });
        }
    }

    private handleSelectionCleared(): void {
        this.updateCursor();
    }
    
    private handleBeforeSelectionCleared(e: fabric.IEvent): void {
         if (e.target && (this.currentTool === "text" || this.currentTool === "emojii")) {
            this.shouldSkipNextAnnotation = true;
        }
    }
    
    // --- Annotation Lifecycle ---
    private beginAnnotating(): void {
        this.isAnnotating = true;
        this.fcanvas.skipTargetFind = true;
        
        if (this.colorPicker.isOpen()) this.colorPicker.close();
        if (this.emojiControl.isOpen()) this.emojiControl.close();

        switch (this.currentTool) {
            case "arrow": this.arrowControl.onMouseDown(this.startPosition!); break;
            case "rectangle": this.rectangleControl.onMouseDown(this.startPosition!); break;
            case "circle": this.circleControl.onMouseDown(this.startPosition!); break;
            case "text": this.textControl.onMouseDown(this.startPosition!); break;
            case "emojii": this.emojiControl.onMouseDown(this.startPosition!); break;
            case "blur": this.blurControl.onMouseDown(this.startPosition!); break;
        }
        this.fcanvas.requestRenderAll();
    }

    private continueAnnotating(): void {
        const currentMouseRect = {
            x1: Math.min(this.startPosition!.x, this.endPosition!.x),
            y1: Math.min(this.startPosition!.y, this.endPosition!.y),
            x2: Math.max(this.startPosition!.x, this.endPosition!.x),
            y2: Math.max(this.startPosition!.y, this.endPosition!.y),
        };

        switch (this.currentTool) {
            case "arrow": this.arrowControl.onMouseMove(this.endPosition!); break;
            case "rectangle": this.rectangleControl.onMouseMove(currentMouseRect); break;
            case "circle": this.circleControl.onMouseMove(currentMouseRect); break;
            case "text": this.textControl.onMouseMove(currentMouseRect); break;
            case "emojii": this.emojiControl.onMouseMove(this.endPosition!); break;
            case "blur": this.blurControl.onMouseMove(currentMouseRect); break;
        }
        this.fcanvas.requestRenderAll();
    }

    private async endAnnotating(): Promise<void> {
        this.isAnnotating = false;
        this.fcanvas.skipTargetFind = false;

        let createdObject: fabric.Object | fabric.Object[] | null = null;
        switch (this.currentTool) {
            case "arrow": createdObject = this.arrowControl.onMouseUp(this.endPosition!); break;
            case "rectangle": createdObject = this.rectangleControl.onMouseUp(); break;
            case "circle": createdObject = this.circleControl.onMouseUp(); break;
            case "text": createdObject = await this.textControl.onMouseUp(); break;
            case "emojii": createdObject = this.emojiControl.onMouseUp(this.endPosition!); break;
            case "blur": createdObject = await this.blurControl.onMouseUp(); break;
            case "pen": createdObject = this.pencilControl.getActiveObject(); break;
        }
        
        if(createdObject){
             const objects = Array.isArray(createdObject) ? createdObject : [createdObject];
             objects.forEach(obj => this.configureObject(obj));
             this.undoRedoManager.addObject(objects);
        }
        
        this.fcanvas.renderAll();
        this.reconfigureSelectableObjects();
        this.updateCursor();
    }

    // --- Tool Management ---
    public toggleTool(tool: string): void {
        this.setCurrentTool(this.currentTool === tool ? null : tool);
        KEventDispatcher.dispatch("toggleTool", { tool: this.currentTool });
        this.updateToolbarUI();
    }

    public toggleToolAction(action: string): void {
        KEventDispatcher.dispatch("toggleToolAction", { action });
        if (action === "copy") this.copyCanvasToClipboard();
        if (action === "download") this.downloadCanvas();
    }
    
    private setCurrentTool(tool: string | null): void {
        this.previousTool = this.currentTool;
        this.currentTool = tool;
        this.fcanvas.isDrawingMode = tool === "pen";
        
        if (tool === "emojii") this.emojiControl.toggle();
        if (tool === "image") {
            this.addImageControl.pickImageAndAddToCanvas();
            // Deselect the image tool after a short delay
            setTimeout(() => this.toggleTool('image'), 500);
        }
        
        tool ? this.enterEditionMode() : this.exitEditionMode();
        this.updateCursor();
    }

    private updateToolbarUI(): void {
        const controls = document.querySelector(".canvas--controls");
        if (controls) {
            controls.querySelectorAll("div").forEach(button => button.classList.remove("active"));
            if (this.currentTool) {
                const button = controls.querySelector(`[data-tool="${this.currentTool}"]`);
                button?.classList.add("active");
            }
        }
    }
    
    // --- Mode Management ---
    private enterEditionMode(): void {
        this.exitEditionMode();
        this.fcanvas.selection = false;
        this.fcanvas.forEachObject(obj => {
            if (!this.undoRedoManager.isElementLocked(obj)) {
                 obj.selectable = false;
                 obj.evented = false;
            }
        });
    }

    private exitEditionMode(): void {
        this.fcanvas.selection = true;
        this.reconfigureSelectableObjects();
    }
    
    private reconfigureSelectableObjects(): void {
        this.fcanvas.forEachObject(obj => {
            if (!this.undoRedoManager.isElementLocked(obj)) {
                obj.evented = true;
                obj.selectable = true;
                obj.setCoords();
            }
        });
        this.fcanvas.requestRenderAll();
    }

    // --- Utility Methods ---
    public setScreenshot(screenshotData: any): void {
        this.screenshot = screenshotData;
        const img = new Image();
        img.crossOrigin = "anonymous";
        img.onload = () => {
            const scale = 1 / (screenshotData.pixelRatio || window.devicePixelRatio);
            const screenshotObject = new fabric.Image(img, {
                left: 0, top: 0, hasControls: false, evented: false, selectable: false,
                scaleX: scale, scaleY: scale
            });
            this.fcanvas.add(screenshotObject);
            this.undoRedoManager.addLockedElement(screenshotObject);
            this.fcanvas.setDimensions({ width: img.width * scale, height: img.height * scale });
            this.fcanvas.requestRenderAll();
        };
        img.src = screenshotData.base64;
    }
    
    public initOverlay(): HTMLDivElement | null {
        if (!this.isOverlay) return null;
        const overlay = document.createElement("div");
        overlay.className = "canvas--overlay";
        setTimeout(() => {
            overlay.style.width = `${this.fcanvas.getWidth()}px`;
            overlay.style.height = `${this.fcanvas.getHeight()}px`;
        }, 100);
        return overlay;
    }

    public setCurrentColor(color: string): void {
        this.currentColor = color;
        if(this.fcanvas.freeDrawingBrush){
             (this.fcanvas.freeDrawingBrush as fabric.PencilBrush).color = color;
        }
        this.updateActiveObjectColor();
        const dots = document.querySelector("#canvas-colorpicker .dots");
        if (dots) (dots as HTMLElement).style.backgroundColor = color;
    }
    
    private updateActiveObjectColor(): void {
        const activeObject = this.fcanvas.getActiveObject();
        if (!activeObject) return;

        const targets = activeObject.type === "activeSelection"
            ? (activeObject as fabric.ActiveSelection).getObjects()
            : [activeObject];

        targets.forEach(obj => {
            console.log(obj.type);
            // This logic needs to be more specific based on object type
            if (obj.type === 'path' || obj.type === 'rect' || obj.type === 'ellipse') {
                obj.set('stroke', this.currentColor);
            }
            

            // group
            if (obj.type === 'group') {
                console.log("group" , obj);
                (obj as fabric.Group).forEachObject((o: fabric.Object) => {
                    o.set('stroke', this.currentColor);
                    o.set('fill', this.currentColor);
                });
            }
            if (obj.type === 'textbox' || (obj as any)._objects) { // Text or Group (Arrow)
                console.log("textbox" , obj);
                obj.set('fill', this.currentColor);
            }
        });

        this.fcanvas.requestRenderAll();
    }

    public updateCursor(): void {
        if (this.fcanvas.getActiveObjects().length > 0) {
            this.fcanvas.defaultCursor = "default";
        } else if (this.currentTool === "text") {
            this.fcanvas.defaultCursor = "text";
        } else if (this.currentTool && ["rectangle", "arrow", "emojii", "circle", "blur"].includes(this.currentTool)) {
            this.fcanvas.defaultCursor = "crosshair";
        } else if (this.fcanvas.isDrawingMode) {
             this.fcanvas.freeDrawingCursor = `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20"><circle cx="10" cy="10" r="2" fill="${this.currentColor}" /></svg>') 10 10, crosshair`;
        }
        else {
            this.fcanvas.defaultCursor = "default";
        }
    }
    
    public configureObject(object: fabric.Object): void {
        object.set({
            borderOpacityWhenMoving: 1,
            borderColor: object.type !== "textbox" ? "rgba(0, 0, 0, 0.2)" : undefined,
            perPixelTargetFind: ['rect', 'path', 'group'].includes(object.type!),
        });
    }
    
    private configureKeystrokes(): void {
        window.addEventListener('keydown', (e) => {
             if ((document.activeElement as HTMLElement).tagName !== 'BODY') return;
            if (e.key === 'Delete' || e.key === 'Backspace') {
                this.undoRedoManager.deleteSelection();
            }
            if ((e.ctrlKey || e.metaKey) && e.key === 'z') {
                this.undoRedoManager.undo();
            }
            if ((e.ctrlKey || e.metaKey) && e.key === 'y') {
                this.undoRedoManager.redo();
            }
        });
    }

    private async copyCanvasToClipboard(): Promise<void> {
        this.fcanvas.discardActiveObject();
        this.fcanvas.renderAll();
        const dataUrl = this.fcanvas.toDataURL({ format: 'png', quality: 1.0 });
        try {
            const blob = await (await fetch(dataUrl)).blob();
            await navigator.clipboard.write([
                new ClipboardItem({ 'image/png': blob })
            ]);
            console.log('Image copied to clipboard');
        } catch (err) {
            console.error('Failed to copy image: ', err);
        }
    }
    
    private async downloadCanvas(): Promise<void> {
        this.fcanvas.discardActiveObject();
        this.fcanvas.renderAll();
        const dataUrl = this.fcanvas.toDataURL({ format: 'png', quality: 1.0 });
        const link = document.createElement('a');
        const timestamp = new Date().toISOString().replace(/[:.]/g, '-');
        link.download = `canvas-export-${timestamp}.png`;
        link.href = dataUrl;
        link.click();
    }
}
