import { fabric } from 'fabric';
import { CanvasAnnotator } from '../CanvasAnnotator';

const emojiList = {
    "Smiling & Affectionate": ["😀", "😃", "😄", "😁", "😆", "😅", "🤣", "😂", "🙂", "😉"],
    "Hands & Body Parts": ["👋", "🤚", "🖐️", "✋", "🖖", "👍", "👎", "✊", "👊", "👏"],
};

export class EmojiControl {
    private canvasAnnotator: CanvasAnnotator;
    private isOpenState: boolean = false;
    private emojiPickerElement: HTMLElement | null = null;
    private currentEmoji: string = "👍";
    private activeObject: fabric.Text | null = null;
    private startPosition: { x: number, y: number };


    constructor(annotator: CanvasAnnotator) {
        this.canvasAnnotator = annotator;
        this.startPosition = {x:0 , y:0}
    }
    
    public isOpen = () => this.isOpenState;

    public toggle(): void {
        this.isOpenState ? this.close() : this.open();
    }
    
    public onMouseDown(position: { x: number, y: number }): void {
        this.startPosition = position;
        this.activeObject = new fabric.Text(this.currentEmoji, {
            left: position.x,
            top: position.y,
            scaleX: 0,
            scaleY: 0,
            fontFamily: "sans-serif",
            originX: 'center',
            originY: 'center',
        });
        this.canvasAnnotator.fcanvas.add(this.activeObject);
    }
    
    public onMouseMove(position: { x: number, y: number }): void {
        if(!this.activeObject) return;
        const dx = position.x - this.startPosition.x;
        const dy = position.y - this.startPosition.y;
        const distance = Math.sqrt(dx * dx + dy * dy);
        const scale = distance / 20; // Adjust sensitivity
        this.activeObject.set({ scaleX: scale, scaleY: scale });
    }
    
    public onMouseUp(position: { x: number, y: number }): fabric.Object | null {
         if (!this.activeObject) return null;
        if (this.activeObject.scaleX === 0) {
            this.activeObject.set({ scaleX: 1, scaleY: 1 });
        }
        this.activeObject.lockUniScaling = true;
        this.canvasAnnotator.fcanvas.setActiveObject(this.activeObject);
        const finalObject = this.activeObject;
        this.activeObject = null;
        return finalObject;
    }

    public open(): void {
        if (this.isOpenState) return;
        this.isOpenState = true;
        // Implementation to create and show the emoji picker UI
        // This is a simplified version.
        const picker = document.createElement('div');
        picker.className = 'canvas--emojis';
        Object.values(emojiList).flat().forEach(emoji => {
            const btn = document.createElement('button');
            btn.innerHTML = emoji;
            btn.onclick = () => {
                this.currentEmoji = emoji;
                this.close();
            };
            picker.appendChild(btn);
        });
        // document.body.appendChild(picker);
        document.querySelector("#canvas-emojii-wrapper")?.appendChild(picker);
        this.emojiPickerElement = picker;
    }

    public close(): void {
        if (!this.isOpenState || !this.emojiPickerElement) return;
        this.isOpenState = false;
        this.emojiPickerElement.remove();
        this.emojiPickerElement = null;
    }
}
