import { fabric } from 'fabric';
import { CanvasAnnotator } from '../CanvasAnnotator';

export class PencilControl {
    private canvasAnnotator: CanvasAnnotator;
    private activeObject: fabric.Path | null = null;

    constructor(annotator: CanvasAnnotator) {
        this.canvasAnnotator = annotator;
        (this.canvasAnnotator.fcanvas.freeDrawingBrush as fabric.PencilBrush).width = 5;
    }
    
    public getActiveObject = () => this.activeObject;

    public onPathCreated(e: { path: fabric.Path }): void {
        this.activeObject = e.path;
        this.activeObject.set({
             shadow: new fabric.Shadow({
                offsetX: 0,
                offsetY: 1,
                blur: 2,
                color: "rgba(0, 0, 0, 0.1)",
                affectStroke: true,
            })
        });
        // The undo/redo logic for path is handled differently because it's created on mouse up automatically by fabric
        this.canvasAnnotator.getUndoRedoManager().addObject([this.activeObject]);
        this.activeObject = null;
    }
}
