import { fabric } from 'fabric';
import { CanvasAnnotator } from './CanvasAnnotator';

enum HistoryAction {
    ADDED_OBJECTS = "added_objects",
    REMOVED_OBJECTS = "removed_objects"
}

interface HistoryItem {
    action: HistoryAction;
    objects: fabric.Object[];
}

export class UndoRedoManager {
    private canvasAnnotator: CanvasAnnotator;
    private undoHistory: HistoryItem[] = [];
    private redoHistory: HistoryItem[] = [];
    private lockedElements: fabric.Object[] = [];

    constructor(annotator: CanvasAnnotator) {
        this.canvasAnnotator = annotator;
    }

    public addObject(objects: fabric.Object[]): void {
        this.undoHistory.push({
            action: HistoryAction.ADDED_OBJECTS,
            objects: objects
        });
        this.redoHistory = []; // Clear redo history on new action

        this.updateHistoryButtons();
    }

    public removeObjects(objects: fabric.Object[]): void {
        this.undoHistory.push({
            action: HistoryAction.REMOVED_OBJECTS,
            objects: objects
        });
        this.redoHistory = [];
        this.updateHistoryButtons();
    }

    public undo(): void {
        const lastAction = this.undoHistory.pop();
        if (!lastAction) return;

        if (lastAction.action === HistoryAction.ADDED_OBJECTS) {
            this.removeObjectsFromCanvas(lastAction.objects);
        } else if (lastAction.action === HistoryAction.REMOVED_OBJECTS) {
            this.addObjectsToCanvas(lastAction.objects);
        }

        this.redoHistory.push(lastAction);
        this.updateHistoryButtons();
        this.canvasAnnotator.fcanvas.requestRenderAll();
    }

    public redo(): void {
        const lastAction = this.redoHistory.pop();
        if (!lastAction) return;

        if (lastAction.action === HistoryAction.ADDED_OBJECTS) {
            this.addObjectsToCanvas(lastAction.objects);
        } else if (lastAction.action === HistoryAction.REMOVED_OBJECTS) {
            this.removeObjectsFromCanvas(lastAction.objects);
        }

        this.undoHistory.push(lastAction);
        this.updateHistoryButtons();
        this.canvasAnnotator.fcanvas.requestRenderAll();
    }
    
    // public addLockedElement(element: fabric.Object): void {
    //     this.lockedElements.push(element);
    //     element.sendToBack();
    // }

    public addLockedElement(element: fabric.Object): void {
        this.lockedElements.push(element);
    
        // If there are locked elements already, place above the last locked element
        if (this.lockedElements.length > 1) {
            const lastLocked = this.lockedElements[this.lockedElements.length - 2];
            const index = this.canvasAnnotator.fcanvas.getObjects().indexOf(lastLocked);
            element.moveTo(index + 1);
        } else {
            // If it's the first locked element, put it at very bottom (likely the screenshot)
            element.sendToBack();
        }
    }
    
    public isElementLocked(element: fabric.Object): boolean {
        return this.lockedElements.includes(element);
    }

    private addObjectsToCanvas(objects: fabric.Object[]): void {
        objects.forEach(obj => this.canvasAnnotator.fcanvas.add(obj));
    }

    private removeObjectsFromCanvas(objects: fabric.Object[]): void {
        objects.forEach(obj => this.canvasAnnotator.fcanvas.remove(obj));
    }

    private updateHistoryButtons(): void {
        const undoButton = document.getElementById("undoButton");
        const redoButton = document.getElementById("redoButton");
        
        if (undoButton) {
            this.undoHistory.length > 0 ? undoButton.classList.remove("disabled") : undoButton.classList.add("disabled");
        }
        if (redoButton) {
            this.redoHistory.length > 0 ? redoButton.classList.remove("disabled") : redoButton.classList.add("disabled");
        }
    }
    
    public deleteSelection(): void {
        const active = this.canvasAnnotator.fcanvas.getActiveObject();
        if (!active) return;
        
        this.canvasAnnotator.fcanvas.discardActiveObject();
        
        const objectsToRemove = active.type === "activeSelection" ? (active as fabric.ActiveSelection).getObjects() : [active];
        
        this.removeObjectsFromCanvas(objectsToRemove);
        this.removeObjects(objectsToRemove); // For history
        
        this.canvasAnnotator.fcanvas.requestRenderAll();
    }
}
