import { CanvasAnnotating } from './CanvasAnnotating';


class ImageEditor{
    private canvasApp: CanvasAnnotating;
    constructor(base64Url: string) {
        this.init(base64Url);
    }   

    init(base64Url: string) {
        this.canvasApp = new CanvasAnnotating();
        // create canvas element and set image as a bg
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d', { willReadFrequently: true })!;
        const img = new Image();
        img.src = base64Url;
        img.onload = () => {
            canvas.width = img.width;
            canvas.height = img.height;
            ctx.drawImage(img, 0, 0);
            const sampleImage = canvas.toDataURL();
            
            // Initialize the canvas editor. Set the second parameter to true
            // if you want to use an overlay, or false otherwise.
            this.canvasApp.init(sampleImage, false);
        };
    }

    // distroy the editor
    distroy() {
        this.canvasApp.distroy();
    }
}

(window as any).ImageEditor = ImageEditor;
